.heros {
  background: url("../../assets/LM Logo.jpg");
  background-size: 100%; }

.title1 {
  color: white;
  font-weight: bold;
  font-size: 65px;
  text-align: center; }

@media screen and (max-width: 768px) {
  .heros {
    height: 600px; } }

@media screen and (min-width: 1000px) {
  .heros {
    height: 800px; } }

.btn-home {
  background-color: white;
  padding: 8px;
  border-radius: 20px;
  color: black;
  width: 150px; }

.btn-home:hover {
  background-color: pink; }

.cards {
  border-radius: 30px;
  max-width: 100%;
  height: 250px;
  background-color: whitesmoke;
  color: black;
  padding: 15px;
  text-align: center;
  position: relative; }

.cards1 {
  border-radius: 30px;
  max-width: 100%;
  height: 250px;
  background-color: lightblue;
  color: black;
  padding: 15px;
  text-align: center;
  position: relative; }

.cards:hover {
  background-color: salmon;
  box-shadow: 5px 4px 8px 7px rgba(0, 0, 0, 0.2); }

.cards1:hover {
  background-color: blue;
  color: white;
  box-shadow: 5px 4px 8px 7px rgba(0, 0, 0, 0.2); }

hr.new4 {
  border: 2px solid black;
  width: 160px; }
