@import 'https://www.w3schools.com/w3css/4/w3.css';
.heros {
  background: url("../../assets/LM Logo.jpg");
  background-size: cover; }

@media screen and (max-width: 768px) {
  .heros {
    background-size: 100%; } }

@media screen and (min-width: 1000px) {
  .heros {
    height: 600px; } }
  @media screen and (min-width: 1000px) and (max-width: 768px) {
    .heros .title3 {
      font-weight: bold; } }
  @media screen and (min-width: 1000px) and (min-width: 769px) {
    .heros .title3 {
      font-size: 2.5rem; } }
  @media screen and (min-width: 1000px) and (min-width: 1000px) {
    .heros .title3 {
      font-size: 30px;
      margin-top: 2rem; } }

.containerr {
  border: 2px solid #ccc;
  background-color: #eee;
  border-radius: 5px;
  padding: 16px;
  margin: 16px 0; }
  .containerr::after {
    content: "";
    clear: both;
    display: table; }
  .containerr img {
    float: left;
    margin-right: 20px;
    border-radius: 50%; }
  .containerr span {
    font-size: 20px;
    margin-right: 15px; }
    @media screen and (max-width: 768px) {
      .containerr span .containerr img .containerr {
        text-align: center; }
      .containerr span .containerr img .containerr img {
        margin: auto;
        float: none;
        display: block; } }

