.containerr {
  border: 2px solid #ccc;
  background-color: #eee;
  border-radius: 5px;
  padding: 16px;
  margin: 16px 0; }
  .containerr::after {
    content: "";
    clear: both;
    display: table; }
  .containerr img {
    float: left;
    margin-right: 20px;
    border-radius: 50%; }
  .containerr span {
    font-size: 20px;
    margin-right: 15px; }
    @media screen and (max-width: 768px) {
      .containerr span .containerr img .containerr {
        text-align: center; }
      .containerr span .containerr img .containerr img {
        margin: auto;
        float: none;
        display: block; } }

